.chart-legend {
    padding-top: 2.5vh;

    &::after {
        content: "";
        display: table;
        clear: both;
    }

    & .legend-button {
        box-sizing: border-box;
        display: flex;
        float: left;
        margin: 0 16px 16px 0;
        padding-right: 16px;
        align-items: center;
        border-radius: 50px;
        border: 1px solid var(--legend-btn-border-color, grey);
        font-size: medium;
        color: var(--text-color, black);
        transition: all var(--theme-transition-duration) ease-in-out;
        transition-property: color, border-color, opacity;
        cursor: pointer;
        -webkit-tap-highlight-color:  rgba(255, 255, 255, 0);

        &:active {
            opacity: .6;
        }

        & > input {
            position: absolute;
            visibility: hidden;

            &:checked + .custom-checkbox::after {
                transform: scale(0);
            }
        }

        & > .custom-checkbox {
            position: relative;
            box-sizing: border-box;
            width: 24px;
            height: 24px;
            border-radius: 50px;
            margin: 7px 16px 7px 7px;
            background: no-repeat center center / 16px transparent;
            background-image: url('data:image/svg+xml;base64,');


            &::after {
                content: "";
                position: absolute;
                top: 2px;
                left: 2px;
                right: 2px;
                bottom: 2px;
                border-radius: 50px;
                z-index: 1;
                background: var(--background, white);
                transform: scale(1);
                transform-origin: center center;
                transition: transform 200ms ease-in-out;
            }
        }
    }

    @media (--narrow-landscape) {
        & .legend-button {
            width: 70%;
            margin: 0 0 7px 20%;
        }
    }
}
